*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --logoRed: rgb(118, 9, 9);
  --logoBright: rgb(227, 223, 200);
  --dark: rgba(31, 33, 43, 0.866);
  --glowColor: #fff;
}

body {
  background-color: rgb(0, 0, 0);
  font-family: "Black Ops One", sans-serif;
}
